.movie-list {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 20px;
	.movie-card,
	.card {
		&__image {
			position: relative;
			line-height: 0;
			border-radius: 4px;
			overflow: hidden;
			&::before {
				content: '';
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-image: linear-gradient(
					to bottom,
					rgba(0, 0, 0, 0),
					rgba(0, 0, 0, 0.6)
				);
			}

			img {
				width: 100%;
				display: inline-block;
				border-radius: 4px;
			}
			.rating-point {
				position: absolute;
				bottom: 15px;
				right: 20px;
				border-radius: 4px;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				gap: 5px;
				line-height: 1;
				.vote-point {
					font-size: 20px;
					// margin-right: 5px;
					// background: #000;
					color: white;
					font-weight: bold;
					// padding: 5px;
				}
				.vote-count {
					font-size: 10px;
					color: white;
					border-radius: 4px;
				}
			}
			.language-and-yeaer {
				line-height: 1;
				position: absolute;
				bottom: 15px;
				left: 20px;
				// border-radius: 4px;
				// background: #000;
				color: white;
				// padding: 5px;
				font-size: 10px;
				.language {
					text-transform: uppercase;
				}
				.year {
					// font-size: 20px;
					margin-left: 5px;
				}
			}
		}
		h2 {
			font-size: 20px;
			font-weight: bold;
			margin-bottom: 5px;
		}
		.overview {
			overflow: hidden;
			text-overflow: ellipsis;
			display: -webkit-box;
			-webkit-line-clamp: 4; /* number of lines to show */
			line-clamp: 4;
			-webkit-box-orient: vertical;
		}
		&__footer {
			.genre {
				ul {
					padding: 0;
					margin: 0;
				}
				li {
					list-style-type: none;
					display: inline-block;
					/* border: solid 1px; */
					border-radius: 4px;
					font-size: 10px;
					margin: 0px 5px 0px 0px;
					text-transform: uppercase;
					background: #eee;
					padding: 3px;
				}
			}
		}
	}
}
